<template>
  <div>
    <div class="user flex-center-y">
      <img src="../../assets/images/mall_submit_add@2x.png" class="address_icon" />
      <div class="flex_1">
        <div class="flex-between">
          <span>收货人：{{data.address.consignee}}</span>
          <span style="margin-left:auto;">{{data.address.mobile}}</span>
        </div>
        <p
          class="address"
          v-if="data.address.city"
        >{{ data.address.city + data.address.district + data.address.address}}</p>
      </div>
      <van-icon name="arrow" class="arrow address_arrow" @click="getAddressList" />
      <!--  <img src="../assets/images/mall_submit_add@2x.png" class="submitOrder_bg" /> -->
    </div>
    <div class="wrap_img">
      <img src="../../assets/images/mall_submit_bg@2x.png" style="width:100%;height:100%;" />
    </div>
    <div class="list">
      <div class="info flex" v-for="item in data.cartList" :key="item.goods_id">
        <img :src="item.original_img" class="product_img" />
        <div class="flex-between">
          <p class="title">{{item.goods_name}}</p>
          <!--   <p class="info_color">白色</p> -->
        </div>
        <div class="flex_right">
          <p class="price">￥{{item.shop_price}}</p>
          <p class="num">x{{item.goods_num}}</p>
        </div>
      </div>
      <!--       <div class="item flex-between" @click="couponShow=true">
        <p class="title">优惠券</p>
        <p class="select_cop">{{coupon == '' ? '选择优惠劵' : '-'+coupon.expression}}</p>
        <van-icon name="arrow" class="arrow" />
      </div>-->
      <!--       <div class="item flex-between">
        <div
          class="title"
        >使用{{data.pay_points?data.pay_points:0}}积分抵扣{{data.pay_points?data.pay_points:0}}元</div>
        <div class="right flex-center-y" style="margin-left:auto;">
          <van-switch v-model="pay_points" />
        </div>
      </div>-->
      <div class="item flex-between">
        <div class="title">备注</div>
        <input type="text" placeholder="请输入要备注的内容" class="flex_1" v-model="user_note" />
      </div>
      <div class="price_info">
        <p class="flex-between">
          <span class="info_title">商品金额：</span>
          <span class="price">￥{{total}}</span>
        </p>
        <p class="flex-between">
          <span class="info_title">运费：</span>
          <span class="price">+￥0.00</span>
        </p>
        <p class="flex-between">
          <span class="info_title">优惠券抵扣：</span>
          <span
            class="price"
          >-￥{{coupon !== '' ? coupon.expression?coupon.expression:'0.00' : '0.00' }}</span>
        </p>
        <p class="flex-between">
          <span class="info_title">积分抵扣：</span>
          <span class="price">-￥{{pay_points ? data.pay_points?data.pay_points:'0.00' : '0.00'}}</span>
        </p>
      </div>
    </div>
    <div class="button_wrap flex-between">
      <p>
        需支付：
        <span>￥{{price}}</span>
      </p>
      <div class="submit flex-center" @click="submit">提交订单</div>
    </div>

    <!--优惠劵-->
    <van-popup v-model="couponShow" position="bottom">
      <div class="coupon_list">
        <div
          class="item flex-center-y"
          v-for="(item, index) in data.userCartCouponList"
          :key="index"
        >
          <div class="left flex-center">
            <div class="num">{{item.expression}}</div>
            <div class="text">{{item.content}}</div>
          </div>
          <div class="center flex_1">
            <p class="title">{{item.name}}</p>
            <p class="time">有效期至 [{{item.end_time}}]</p>
          </div>
          <div class="right flex-center" @click="use(item)">
            <div class="button flex-center">去使用</div>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Icon, Switch, Popup, Toast } from "vant";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  components: {
    "van-icon": Icon,
    "van-switch": Switch,
    "van-popup": Popup
  },
  data() {
    return {
      pay_points: false,
      user_money: false,
      couponShow: false,
      coupon: "",
      address: "",
      addressShow: false,
      data: {
        address: {},
        cartList: []
      },
      user_note: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      if (this.$route.query.action == "") {
        let params = new URLSearchParams();
        params.append("cart_ids", this.$route.query.ids);
        params.append("openid", localStorage.getItem("openid"));
        axios.post(url.bugNow, params).then(res => {
          if (res.data.code === 400) {
            Toast("您还没有添加默认收货地址，请添加或设置默认收货地址");
          } else {
            this.data = res.data.data;
          }
        });
      } else {
        let data = new URLSearchParams();
        data.append("goods_id", this.$route.query.goods_id);
        data.append("openid", localStorage.getItem("openid"));
        data.append("goods_num", this.$route.query.goods_num);
        data.append("action", "buy_now");
        data.append("item_id", this.$route.query.item_id);
        axios.post(url.bugNow, data).then(res => {
          if (res.data.code === 400) {
            Toast("您还没有添加默认收货地址，请添加或设置默认收货地址");
          } else {
            this.data = res.data.data;
          }
        });
      }
    },
    // 使用优惠劵
    use(item) {
      this.coupon = item;
      this.couponShow = false;
    },
    // 选择收货地址
    useAddress(item) {
      this.address = item;
      this.addressShow = false;
    },
    // 获取地址列表
    getAddressList() {
      this.$router.push("/address");
    },
    // 提交订单
    submit() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("goods_id", this.data.cartList[0].goods_id);
      params.append("goods_num", this.data.cartList[0].goods_num);
      /* params.append("item_id", this.data.cartList[0].item_id); */
      params.append("action", "buy_now");
      params.append("address_id", this.data.address.address_id);
      params.append("coupon_id", "");
      params.append("pay_points", "");
      params.append("user_money", "");
      params.append("user_note", this.user_note);
      /*    if (this.$route.query.action == "") {
        data.cart_ids = this.data.cart_ids.toString();
      } else {
        data.goods_id = this.$route.query.goods_id;
        data.goods_num = this.$route.query.goods_num;
        data.item_id = this.$route.query.item_id;
      } */

      //支付
      axios.post(url.addOrder, params).then(res => {
        if (typeof WeixinJSBridge == "undefined") {
          if (document.addEventListener) {
            document.addEventListener(
              "WeixinJSBridgeReady",
              this.onBridgeReady(res),
              false
            );
          } else if (document.attachEvent) {
            document.attachEvent(
              "WeixinJSBridgeReady",
              this.onBridgeReady(res)
            );
            document.attachEvent(
              "onWeixinJSBridgeReady",
              this.onBridgeReady(res)
            );
          }
        } else {
          this.onBridgeReady(res);
        }
      });
    },
    onBridgeReady(res) {
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: res.data.data.appId, //公众号名称，由商户传入
          timeStamp: res.data.data.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: res.data.data.nonceStr, //随机串
          package: res.data.data.package,
          signType: res.data.data.signType, //微信签名方式：
          paySign: res.data.data.paySign //微信签名
        },
        res => {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            this.$router.push({ path: "/my" });
          }
        }
      );
    }
  },
  computed: {
    // 总价
    total() {
      let total = 0;
      this.data.cartList.forEach((val, key) => {
        total += Number(val.shop_price) * val.goods_num;
      });
      return total.toFixed(2);
    },
    // 成交价
    price() {
      let price = Number(this.total);
      if (this.user_money) price = price - this.data.user_money;
      if (this.pay_points) price = price - this.data.pay_points;
      if (this.coupon !== "") price = price - this.coupon.expression;
      return price.toFixed(2);
    }
  }
};
</script>

<style scoped lang="scss">
.user {
  padding: 10px 10px 10px 30px;
  background: #fff;
  position: relative;
  .address_icon {
    width: 30px;
    position: absolute;
    left: 10px;
    top: 40px;
  }
  .flex_1 {
    padding: 0 20px;
    overflow: hidden;
    .flex-between {
      font-size: 15px;
      margin-bottom: 5px;
      color: #000;
      font-weight: bold;
      display: flex;
    }
    .address {
      line-height: 25px;
      color: #4c4c4c;
    }
  }
  .address_arrow {
    position: absolute;
    right: 10px;
    top: 38px;
  }
  .submitOrder_bg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}
.list {
  padding: 0 10px;
  background: #fff;
  margin-bottom: 100px;
  .info {
    padding: 30px 0;
    border-bottom: 1px solid #ebebeb;
    display: flex;
    .product_img {
      width: 60px;
      height: 60px;
      border-radius: 14px;
    }
    .flex-between {
      margin-left: 10px;
    }
    .flex_right {
      margin-left: auto;
    }
    .title {
      font-size: 14px;
      font-family: SourceHanSansCN-Medium;
      font-weight: 500;
      color: #454545;
      line-height: 13px;
    }
    .info_color {
      font-size: 12px;
      font-family: SourceHanSansCN-Normal;
      font-weight: 400;
      color: rgba(69, 69, 69, 1);
      line-height: 13px;
      margin-top: 14px;
    }
    .price {
      font-size: 16px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(220, 33, 33, 1);
      line-height: 13px;
    }
    .text {
      margin: 8px 0;
    }
    .num {
      text-align: right;
      font-size: 14px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 13px;
      margin-top: 30px;
    }
  }
  .item {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #ebebeb;
    input {
      text-align: right;
      border: none;
      margin-left: auto;
    }
    .title {
      font-size: 15px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(0, 0, 0, 1);
      line-height: 9px;
    }
    .select_cop {
      font-size: 13px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(78, 78, 78, 1);
      line-height: 9px;
      margin-left: auto;
    }
  }
  .price_info {
    overflow: hidden;
    color: #020202;
    p {
      margin: 10px 0;
      .info_title {
        font-size: 13px;
        font-family: SourceHanSansCN-Regular;
        font-weight: 400;
        color: rgba(2, 2, 2, 1);
        line-height: 30px;
      }
      .price {
        font-size: 15px;
        font-family: SourceHanSansCN-Medium;
        font-weight: 500;
        color: rgba(239, 24, 24, 1);
        line-height: 30px;
      }
    }
  }
}
.button_wrap {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: #fff;
  box-shadow: 0 0 10px #eee;
  p {
    flex: 1;
    font-size: 13px;
    font-family: SourceHanSansCN-Regular;
    font-weight: 400;
    color: rgba(2, 2, 2, 1);
    line-height: 22px;
    display: flex;
    align-items: center;
    margin-left: 10px;
    span {
      color: #ef1818;
      font-weight: bold;
    }
  }
  .submit {
    width: 133px;
    color: #fff;
    font-size: 18px;
    background: #ff4c23;
    line-height: 50px;
    text-align: center;
    letter-spacing: 2px;
  }
}
.coupon_list {
  padding: 30px;
  min-height: 50vh;
  .item {
    background: url(../../assets/images/mall_submit_on@2x.png) no-repeat center
      center;
    background-size: 100% 100%;
    height: 200px;
    margin-top: 20px;
    .left {
      width: 180px;
      color: #fff;
      height: 100%;
      flex-direction: column;
      .num {
        font-size: 60px;
      }
      .text {
        font-size: 22px;
        margin-top: 4px;
      }
    }
    .center {
      overflow: hidden;
      padding: 0 20px;
      .title {
        font-size: 30px;
      }
      .time {
        color: #939393;
        margin-top: 30px;
        font-size: 22px;
      }
    }
    .right {
      width: 170px;
      .button {
        width: 120px;
        height: 40px;
        border-radius: 30px;
        border: 2px solid #2198e6;
        color: #2198e6;
      }
      .img {
        width: 140px;
      }
    }
  }
  .disabled {
    background: url(../../assets/images/mall_submit_off@2x.png) no-repeat center
      center;
    background-size: 100% 100%;
  }
}

.address_list {
  padding: 0 30px;
  background: #fff;
  width: 100vw;
  min-height: 50vh;
  box-sizing: border-box;
  .item {
    padding: 40px 0;
    border-bottom: 1px solid #eee;
    .left {
      overflow: hidden;
      padding-right: 100px;
      .column {
        font-size: 32px;
        color: #201b18;
        .phone {
          margin-left: 30px;
        }
        .default {
          font-size: 18px;
          padding: 2px 14px;
          border-radius: 30px;
          background: #2198e6;
          color: #fff;
          margin-left: 6px;
        }
      }
      .address {
        margin-top: 20px;
        color: #404040;
      }
    }
    .right {
      width: 170px;
      color: #2198e6;
    }
  }
}
</style>



